.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 10s linear;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

icon-row a {
  display: inline-block;
}

.icon {
  color: white;
  text-decoration: none;
  padding: 5px;
  font-size: 1.5em;
}

.icon.big {
  font-size: 2em;
}

.icon:hover {
  color: #cccccc;
}

#tagline {
  transition: 300ms all ease;
  opacity: 1;
  will-change: opacity;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.hide {
  opacity: 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
